import { Grid, Typography } from '@mui/material';
import MainCard from 'ui-component/cards/MainCard';
import ReactMarkdown from 'react-markdown';
import { useEffect } from 'react';

const markdownText = `
Welcome to our Privacy Policy page. We are committed to protecting your privacy and handling your personal information with care. This Privacy Policy explains how we collect, use, disclose, and protect your data when you use our website or services. By accessing or using our website or services, you agree to the terms outlined in this Privacy Policy.
***
## Collection of Information

We collect personal data when you voluntarily provide it to us, such as when you fill out forms, subscribe to our newsletter, or interact with our website. The information we collect may include your name, email address and other relevant details.

## Use of Information

We use the collected information to provide and improve our services, personalize your experience, communicate with you, and send you relevant updates offers. We may also use your information for analytical purposes to understand and enhance our website's performance and user experience.

## Disclosure of Information

We do not sell, trade, or otherwise transfer your personal information to third parties without your consent, except as described in this Privacy Policy. We may disclose your information to trusted third-party service providers who assist us in operating our website and conducting our business, subject to strict confidentiality agreements.

## Data Security

We take reasonable measures to protect the security and integrity of your personal information. We use industry-standard security technologies and procedures to prevent unauthorized access, misuse, alteration, or loss of data. However, please note that no method of transmission over the internet or electronic storage is completely secure, and we cannot guarantee absolute data security.

## Cookies and Tracking Technologies

Our website may use cookies and other tracking technologies to enhance your browsing experience, analyze trends, administer the site, and gather demographic information. You have the option to disable cookies through your browser settings, although this may limit certain features and functionality of our website.

## Links to Third-Party Websites

Our website may contain links to third-party websites for your convenience or reference. We do not have control over the content, policies, or practices of these websites, and we are not responsible for their privacy practices. We encourage you to review the privacy policies of these third-party websites before providing any personal information.

## Changes to this Privacy Policy

We reserve the right to update or modify this Privacy Policy at any time without prior notice. Any changes will be effective immediately upon posting the updated Privacy Policy on our website. We encourage you to review this page periodically to stay informed about how we collect, use, and protect your personal information.

---

If you have any questions, concerns, or requests regarding this Privacy Policy or the handling of your personal information, please contact us using the information provided below:

- Email: [contact.infolsco@gmail.com](mailto:contact.infolsco@gmail.com)
- Twitch (DM): [twitch.tv/LsCo](https://twitch.tv/LsCo)
- Twitter (DM): [twitter.com/@devLsCo](https://twitter.com/@devLsCo)
`;

const Page = () => {
  useEffect(() => {
    document.title = 'LsCo Software - Privacy Policy';
  }, []);

  return (
    <MainCard title="Privacy Policy" description="Last Updated: July 3, 2023">
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={12}>
          <Typography
            component={ReactMarkdown}
            variant="body1"
            sx={{
              fontSize: 16,
              '& a': {
                color: 'gray'
              }
            }}
          >
            {markdownText}
          </Typography>
        </Grid>
      </Grid>
    </MainCard>
  );
};

export default Page;
